html {
  height: 100%; }

body {
  min-width: 768px;
  background-color: #ffffff;
  margin: 0 auto;
  text-align: center;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-style: normal;
  color: #484848;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow-x: hidden; }

@media (max-width: 767px) {
  body {
    min-width: 320px; } }

@font-face {
  font-family: "Fira Sans";
  font-weight: normal;
  src: url("../fonts/firasans-regular.woff2") format("woff2"), url("../fonts/firasans-regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Fira Sans";
  font-weight: normal;
  src: url("../fonts/firasans-medium.woff2") format("woff2"), url("../fonts/firasans-medium.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Fira Sans";
  font-weight: bold;
  src: url("../fonts/firasans-bold.woff2") format("woff2"), url("../fonts/firasans-bold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  font-weight: bold;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Raleway";
  font-weight: bold;
  src: url("../fonts/raleway-extrabold.woff2") format("woff2"), url("../fonts/raleway-extrabold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Raleway";
  font-weight: normal;
  src: url("../fonts/raleway-black.woff2") format("woff2"), url("../fonts/raleway-black.woff") format("woff");
  font-display: swap; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
  white-space: nowrap;
  clip-path: inset(100%); }

.page-header {
